import { FormControl, FormGroup, Validators } from '@angular/forms';
import { PackageEntity } from '@nequi/features/recharge/core';
import { ConfirmInfoPocket, ConfirmInfoPocketItem } from '@nequi/ui';

import { BuyConfig } from './buy.config';

interface PackageControl extends PackageEntity {
  nameSelected: string;
}

export class BuyViewModel {
  public codeControl: FormControl;
  public emailControl: FormControl;
  public pocketControl: FormControl;
  public form: FormGroup;
  public codesList: [];
  public pocketList = [];

  constructor() {
    this.buildForm();
    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  public get package(): PackageControl {
    return this.codeControl.value;
  }

  public buildForm() {
    this.createControls();

    this.form = new FormGroup({
      pocket: this.pocketControl,
      code: this.codeControl,
      email: this.emailControl,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  public buildArrayConfirmationItems(
    messages: any,
    amount: string,
    email: string,
    product: string
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    return [
      {
        label: messages.items.product,
        value: product,
      },
      {
        label: messages.items.email,
        value: email,
      },
      {
        label: messages.items.amount,
        value: amount,
      },
    ];
  }

  private createControls() {
    this.pocketControl = new FormControl(this.pocketList[0]);
    this.codeControl = new FormControl('', [Validators.required]);
    this.emailControl = new FormControl('', [
      Validators.required,
      Validators.minLength(BuyConfig.validators.email.minLength),
      Validators.maxLength(BuyConfig.validators.email.maxLength),
      Validators.pattern(BuyConfig.validators.email.pattern),
    ]);
  }
}
