import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { UserInfoProvider } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType, PaymentType } from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import {
  NetflixTransaction,
  NetflixTransactionEntity,
  PaymentInProgressException
} from '@nequi/features/netflix/core';
import {
  OperatorEntity,
  OperatorType,
  PackageEntity,
  RechargesInteractor
} from '@nequi/features/recharge/core';
import { CardModalService, ConfirmInfoPocketProps } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { NetflixConfig } from '../../../config/netflix.config';
import { BuyConfig } from './buy.config';
import { BuyViewModel } from './buy.view-model';

@Component({
  selector: 'nequi-buy',
  templateUrl: './buy.page.html',
  styleUrls: ['./buy.page.scss'],
})
export class BuyPage extends TransactionBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;
  public viewModel = new BuyViewModel();
  public config = BuyConfig;
  public balanceToggle = false;
  public operator: OperatorEntity;

  constructor(
    private rechargesInteractor: RechargesInteractor,
    private applicationDataInteractor: ApplicationDataInteractor,
    private userInfoProvider: UserInfoProvider,
    private currencyPipe: CurrencyPipe,
    private cardModalService: CardModalService,
    private analyticsInteractor: AnalyticsInteractor,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(NetflixConfig.i18n.moduleKey);
  }

  public async ngOnInit() {
    this.setEmail();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public howToUseNetflix() {
    this.navigatorProvider.navigateForward(this.config.routes.helpPage);
  }

  public async getPackageList() {
    this.operator = (await this.rechargesInteractor.getOperatorsByType(OperatorType.NETFLIX))[0];

    const packageList = await this.rechargesInteractor.getPackagesByCode(
      this.operator.id.toString()
    );

    return this.getPackageListFormatted(packageList);
  }

  public async makeTransaction() {
    const data: NetflixTransactionEntity = {
      type: PaymentType.RECHARGE,
      merchant: this.operator,
      additionalInfo: {
        email: this.viewModel.emailControl.value,
        package: this.viewModel.codeControl.value,
        operatorType: OperatorType.NETFLIX,
      },
    };

    const transaction = new NetflixTransaction()
      .withData(data)
      .withAmount(this.viewModel.package.amount)
      .withPocket(this.viewModel.pocketControl.value);

    super.executeTransaction(transaction);
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(this.config.i18n.confirmBottomSheet);

    return {
      title: messages.title,
      subtitle: messages.subtitle,
      confirmLabel: messages.title,
      items: this.viewModel.buildArrayConfirmationItems(
        messages,
        this.currencyPipe.transform(this.viewModel.package.amount),
        this.viewModel.emailControl.value,
        this.viewModel.package.nameSelected
      ),
    };
  }

  protected async onTransactionSuccess() {
    try {
      await this.applicationDataInteractor.save(
        NetflixConfig.applicationData.lastEmail,
        this.viewModel.emailControl.value
      );

      await this.navigatorProvider.navigateRoot(this.config.routes.dashBoard);
      this.setAnalyticsSuccessRecharge();
      this.showSuccessModal();
    } catch (error) {
      this.toastService.showError(error.message);
    }
  }

  protected async onTransactionFailed(error: CoreException) {
    if (error instanceof ExceedCeilingException) {
      throw error;
    }

    if (error instanceof PaymentInProgressException) {
      this.showPaymenInProgressModal();

      return;
    }

    this.toastService.showError(error.message);
  }

  private setAnalyticsSuccessRecharge() {
    const analytics = BuyConfig.analytics;
    const clevertapParameters = stringsArrayToObjectWithValues(
      BuyConfig.analytics.clevertap.properties,
      [getFormattedDateTimes(), this.currencyPipe.transform(this.viewModel.package.amount)]
    );

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: analytics.events.netflixSuccessRecharge },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.events.netflixSuccessRecharge },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.clevertap.eventName,
        eventProps: clevertapParameters,
      },
    });
  }

  private getPackageListFormatted(packages: PackageEntity[]) {
    return packages.map((_package) => {
      const amountFormatted = this.currencyPipe.transform(_package.amount);
      const packageMessageFormatted = this.translateProvider.get<string>(
        this.config.i18n.codeFormatBase,
        {
          value: amountFormatted,
        }
      );

      return {
        name: amountFormatted,
        nameSelected: packageMessageFormatted,
        ..._package,
      };
    });
  }

  private async showSuccessModal() {
    const messages = this.translateProvider.get(this.config.i18n.modal.success);

    await this.cardModalService.show({
      title: messages.header,
      imgPath: messages.image,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.accept,
        },
        {
          text: messages.buttons.cancel.text,
          isLink: messages.buttons.cancel.isLink,
          handler: () => this.howToUseNetflix(),
        },
      ],
    });
  }

  private async showPaymenInProgressModal() {
    const messages = this.translateProvider.get(this.config.i18n.modal.paymentInProgress);

    await this.cardModalService.show({
      title: messages.header,
      imgPath: messages.image,
      content: messages.content,
      buttons: [
        {
          text: messages.accept,
        },
      ],
    });
  }

  private async setEmail() {
    await this.loadingService.show();
    this.viewModel.emailControl.setValue(await this.getPreloadEmail());
    await this.loadingService.hide(true);
  }

  private async getPreloadEmail(): Promise<string> {
    const data = await this.applicationDataInteractor.findValue<string>(
      this.config.appData.formDataKey,
      null
    );

    if (data) {
      return data;
    }

    return this.userInfoProvider.getEmail();
  }
}
