import { NgModule, Type } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { InformationBottomSheetMiddleware } from '@nequi/features/commons/presentation';

import { NetflixConfig } from '../config/netflix.config';
import { BuyPage } from './pages/buy/buy.page';
import { HelpPage } from './pages/help/help.page';
import { TermsPage } from './pages/terms/terms.page';

const routes: Routes = [
  {
    path: '',
    component: Type,
    canActivate: [InformationBottomSheetMiddleware],
    data: {
      i18nModuleKey: NetflixConfig.i18n.moduleKey,
      i18nMessagesKey: NetflixConfig.i18n.bottomSheet,
      redirectTo: 'terms',
    },
  },
  {
    path: 'terms',
    component: TermsPage,
  },
  {
    path: 'buy',
    component: BuyPage,
  },
  {
    path: 'help',
    component: HelpPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeaturesNetflixRoutingModule {}
