import { Component, OnInit } from '@angular/core';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { NetflixInteractor } from '@nequi/features/netflix/core';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { NetflixConfig } from '../../../config/netflix.config';
import { TermsConfig } from './terms.config';

@Component({
  selector: 'nequi-terms',
  templateUrl: './terms.page.html',
})
export class TermsPage implements OnInit {
  public config = TermsConfig;
  public contract: string;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private netflixInteractor: NetflixInteractor,
    private translateProvider: TranslateProvider,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    this.translateProvider.loadModule(NetflixConfig.i18n.moduleKey);
  }

  public async ngOnInit() {
    this.contract = await this.netflixInteractor.getContract();

    this.setAnalyticsIntentionOfBegin();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public acceptContract() {
    this.navigatorProvider.navigateForward(this.config.routes.buyPage);
  }

  private setAnalyticsIntentionOfBegin() {
    const analytics = TermsConfig.analytics;
    const clevertapParameters = stringsArrayToObjectWithValues(
      TermsConfig.analytics.clevertap.properties,
      [getFormattedDateTimes()]
    );

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: analytics.events.netflixIntentionOfBegin },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.events.netflixIntentionOfBegin },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.clevertap.eventName,
        eventProps: clevertapParameters,
      },
    });
  }
}
