import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { TranslateProvider } from '@nequi/features/commons/providers';

import { NetflixConfig } from '../../../config/netflix.config';
import { HelpConfig } from './help.config';

@Component({
  selector: 'nequi-help',
  templateUrl: './help.page.html',
})
export class HelpPage implements OnInit {
  public config = HelpConfig;
  public items: Array<{ title: string; description?: string }> = [];

  constructor(private navController: NavController, private translateProvider: TranslateProvider) {}

  public async ngOnInit() {
    await this.translateProvider.loadModule(NetflixConfig.i18n.moduleKey);

    this.items = this.translateProvider.get(this.config.i18n.content);
  }

  public goBack() {
    this.navController.back();
  }
}
